import React from "react";

function Chapter(props) {
    const {books, book, chapter, verses} = props;

    return(
        <div className="chapter">
            {verses.map(verse => (
            verse.verse_no === 1 ?
                <span key={verse._id}>
                <b className="chapter-number">{chapter}</b>
                    &nbsp; &nbsp;
                    {/* <sup>
                        {verse.verse_no}
                    </sup> */}
                    {verse.verse_text}
                </span> :
                <span key={verse._id}>
                    &nbsp;
                    <sup>
                        {verse.verse_no}
                    </sup>
                    {verse.verse_text}
                </span>
                )
            )}
        </div>
    );
}

export default Chapter;